<script>
	const APP = getApp();
	import headerNavigation from "@/components/header/header.vue";
	import {
		getHotGoodsInfo,
		getFirstLevelClass,
		getSecondaryClass,
		getThreeLevelClass,
		getCategoryAnalysis
	} from "@/utils/http.js";
	import {
		formatTime,
		getLanguageData
	} from "@/utils/utils.js";
	export default {
		components: {
			headerNavigation
		},
		data() {
			return {
				tabIndex: 0, //标签页热销商品，品类分析tab下标
				menuIndex: 0, //当前菜单下标，本日，...
				selectData: '', //选择时间的当前文字
				selectValue: 0, //当前下拉选项下标
				range: [{
						value: 0,
						text: "Category Name Class Name"
					},
					{
						value: 1,
						text: "Category Name Class Name"
					},
					{
						value: 2,
						text: "Category Name Class Name"
					}
				],
				status: "more", //more:加载前，loading：加载中，noMore：没有数据了
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				},
				categoryTabIndex: 0, //品类分析tab下标
				timeType: "D",
				timeValue: "",
				hotGoodsList: [],
				CategoryList: [],
				selectName: "",
				level1: [],
				level2: [],
				level3: [],
				lvl1Select: "",
				lvl2Select: "",
				lvl3Select: "",
				org0: "",
				org1: "",
				org2: "",
				org3: "",
				org4: "",
				maleRise: true,
				femaleRise: true,
				neutralRise: true,
				Language:{}
			}
		},
		onLoad() {
			this.$i18n.locale = APP.globalData.code;
			this.selectData = this.$t('Other-months');
			getLanguageData().then((res)=>{
				this.Language=res;
			})
		},
		onReachBottom() {
			// console.log("分页")
		},
		methods: {
			// 获取一级商品分类
			getClass1() {
				let that = this;
				return new Promise((resolve, reject) => {
					getFirstLevelClass().then((res1) => {
						that.level1 = res1;
						that.lvl1Select = res1[0].prod_level1;
						for (const key in res1) {
							if (res1[key].prod_level1 === 'RTW') {
								that.lvl1Select = res1[key].prod_level1;
							}
						}
						resolve(1)
					})
				})
			},
			// 获取二级商品分类
			getClass2() {
				let that = this;
				return new Promise((resolve, reject) => {
					getSecondaryClass({
						level1_code: that.lvl1Select
					}).then((res) => {
						that.level2 = res;
						that.lvl2Select = res[0].prod_level2;
						resolve(2)
					})
				})
			},
			// 获取三级商品分类
			getClass3() {
				let that = this;
				return new Promise((resolve, reject) => {
					getThreeLevelClass({
						level1_code: that.lvl1Select,
						level2_code: that.lvl2Select
					}).then((res) => {
						that.level3 = res;
						if (res && res[0] != undefined && res[0] != '') {
							that.lvl3Select = res[0].prod_level3;
						}
						resolve(3)
					})
				})
			},
			// 当前选中的数据
			menuClick(data) {
				let that = this;
				that.menuIndex = data.menuIndex;
				that.tabIndex = data.tabIndex;
				that.timeValue = data.timeValue;
				that.timeType = data.calendarData;
				that.org0 = data.lvl0Select;
				that.org1 = data.lvl1Select;
				that.org2 = data.lvl2Select;
				that.org3 = data.lvl3Select;
				that.org4 = data.lvl4Select;
				let classArray=[that.getClass1,that.getClass2,that.getClass3];
				let sequence=classArray.reduce((prev,next)=>prev.then(()=>next()),Promise.resolve());
				sequence.then(res => {
					that.selectName = that.lvl1Select + "->" + that.lvl2Select + "->" + that.lvl3Select;
					if (data.tabIndex === 0) {
						that.hotGoodsList = [];
						that.getHotGoods(); //热销商品
					} else {
						that.getClass(); //品类分析
					}
				})
			},
			// 切换品类分析tab
			categoryTab(index) {
				this.categoryTabIndex = index;
			},
			//下拉选择
			selectChange(e) {
				console.log(e)
			},
			// 获取热销商品
			getHotGoods() {
				let that = this;
				that.$showLoading();
				getHotGoodsInfo({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4,
					level1_code: that.lvl1Select,
					level2_code: that.lvl2Select,
					level3_code: that.lvl3Select
				}).then((res) => {
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.hotGoodsList = [];
					} else {
						that.hotGoodsList = res;
					}
				})
			},
			lvl1Click(e) {
				this.lvl1Select = e;
				this.lvl2Select = "";
				this.lvl3Select = "";
				this.getClass2();
				this.getClass3();
			},
			lvl2Click(e) {
				this.lvl2Select = e;
				this.lvl3Select = "";
				this.getClass3();
			},
			lvl3Click(e) {
				this.lvl3Select = e;
			},
			// 显示商品分类弹框
			shwoClass() {
				this.$refs.showRight.open();
			},
			// 关闭商品分类弹框
			closeDrawer() {
				this.$refs.showRight.close();
			},
			// 确定分类
			define() {
				this.getHotGoods();
				this.selectName = this.lvl1Select + "->" + this.lvl2Select + "->" + this.lvl3Select;
				this.$refs.showRight.close();
			},
			// 重置分类
			resetClick() {
				this.lvl1Select = this.level1[0].prod_level1;
				this.getClass2();
			},
			// 品类分析
			getClass() {
				let that = this;
				that.$showLoading();
				getCategoryAnalysis({
					timeType: that.timeType,
					timeValue: that.timeValue,
					org0: that.org0,
					org1: that.org1,
					org2: that.org2,
					org3: that.org3,
					org4: that.org4,
					level1_code: that.lvl1Select,
					level2_code: that.lvl2Select,
					level3_code: that.lvl3Select
				}).then((res) => {
					uni.hideLoading();
					if (res == undefined || res == null || res == '') {
						that.CategoryList = [];
					} else {
						that.CategoryList = res;
					}
				})
			},
			// 男排序
			topMaleRise() {
				let that = this;
				that.maleRise = !that.maleRise;
				that.CategoryList = that.CategoryList.sort((a, b) => {
					if (that.maleRise) {
						return a.m - b.m
					} else {
						return b.m - a.m
					}
				});
			},
			// 女排序
			topFemaleRise() {
				let that = this;
				that.femaleRise = !that.femaleRise;
				that.CategoryList = that.CategoryList.sort((a, b) => {
					if (that.femaleRise) {
						return a.w - b.w
					} else {
						return b.w - a.w
					}
				});
			},
			// 中性排序
			topNeutralRise() {
				let that = this;
				that.neutralRise = !that.neutralRise;
				that.CategoryList = that.CategoryList.sort((a, b) => {
					if (that.neutralRise) {
						return a.x - b.x
					} else {
						return b.x - a.x
					}
				});
			}
		}
	}
</script>

